@import '/src/theme/_base';

.container {
  &__button {
    padding: 16px 32px;
    display: inline-block;
    min-width: auto;
    white-space: nowrap;
    text-align: center;
    color: white;
    background-color: #007bb3;
    font-weight: 500;
    border-radius: 28px;
  }

  &__button.disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
}