@import '/src/theme/_base';

.container {
  &__flexbox {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 16px;
    height: 100vh;
  }

  &__header {
    font-size: 32px;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: -0.02em;
    text-align: center;
    margin-top: 25px;
    color: #000;
  }

  &__description {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
    margin: 25px 0 50px 0;
  }

  &__image {
    margin: 0 auto;
    width: 100%;
    position: relative;
  }

  &__imageOverlay {
    width: 84%;
    height: 55%;
    position: absolute;
    z-index: 2;
    background-color: #8EFF5C;
    opacity: 0.3;
    top: 32%;
    right: 8%;
  }
}
